.wp-list-table {
	border: none !important;
	border-collapse: collapse;
	box-shadow: none !important;
	width: 100%;
	--border-color: var(--admin-redesign-border, #dcdcde);
	--surface-primary: var(--admin-redesign-surface-light-primary, #ffffff);
	--surface-secondary: var(--admin-redesign-surface-light-secondary, #f6f7f7);
	--text-secondary: var(--admin-redesign-text-secondary, #50575e);

	& th,
	& td {
		padding: 12px 16px;
		vertical-align: middle;
		text-align: left;
	}

	& thead th,
	& thead td {
		background-color: var(--surface-secondary);
		border-bottom: 1px solid var(--border-color) !important;
		color: var(--text-secondary);
		font-size: 13px;
		font-weight: 500;
		text-transform: none;
		line-height: 1.4;

		& a {
			align-items: center;
			display: inline-flex;
			gap: 4px;
			font-weight: inherit;
		}
	}

	& tfoot th,
	& tfoot td {
		background-color: var(--surface-secondary);
		border-top: 1px solid var(--border-color) !important;
		color: var(--text-secondary);
		font-size: 13px;
		font-weight: 500;
		text-transform: none;
		line-height: 1.4;

		& a {
			align-items: center;
			display: inline-flex;
			gap: 4px;
			font-weight: inherit;
		}
	}

	& th.sortable a,
	& th.sorted a,
	& th.manage-column:where(:not(:has(a))) {
		padding-block: 12px;
		padding-inline: 16px;
	}

	& tbody tr {
		background-color: var(--surface-primary) !important;
		border-bottom: 1px solid var(--border-color) !important;

		&:last-child {
			border-bottom: none !important;
		}

		&:hover {
			background-color: var(--surface-secondary) !important;
		}

		& td,
		& th {
			font-size: 14px;
			line-height: 1.5;
		}
	}

	& .check-column {
		padding-inline: 8px;
		width: 2.7em;
	}

	& .column-primary {}

	& .row-actions {
		color: var(--text-secondary);
		font-size: 13px;
		padding-top: 2px;
	}
}

.tablenav {
	align-items: center;
	display: flex;
	flex-flow: row wrap;
	gap: 12px;
	height: fit-content;
	width: 100%;

	&.top {
		margin-bottom: 16px;
	}

	&.bottom {
		margin-top: 16px;
		border-top: 1px solid var(--wp-list-table--border-color, #dcdcde);
		padding-top: 16px;
	}

	& .tablenav-pages {
		float: none;
		margin: 0;
		margin-inline-start: auto;
		line-height: 1.5;
	}

	& .bulkactions {
		display: flex;
		align-items: center;
		gap: 8px;
	}
}